.b-action {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	z-index: 1;
	width: 15.2rem;
	height: 15.2rem;
	background: #ffffff url("data:image/png;base64,") center no-repeat;
	background-size: 13.8rem;
	color: $color-dark;
	padding: 2rem;
	margin: 0 auto;
	box-shadow: 0 00px 60px rgba(28, 46, 68, 0.3);
	@include radius(50%);
	&:hover,
	&:focus {
		@include scale(1.1); }
	&__icon {
		@include scale(.8);
		margin-bottom: .5rem; }
	&__title {
		font-weight: 700;
		text-align: center; }
	@include min($lg) {
		width: 17.2rem;
		height: 17.2rem;
		box-shadow: 0 20px 60px rgba(28, 46, 68, 0.3);
		background-size: auto;
		&__icon {
			margin-bottom: 1rem;
			@include scale(1); } } }


