.b-vertical-title {
	position: absolute;
	top: 44%;
	display: none;
	align-items: center;
	@include rotate(-90deg);
	&__border {
		width: 8px;
		height: 48px;
		background: transparent url(data:image/svg+xml;base64,) center no-repeat;
		background-size: cover;
		@include rotate(-90deg); }
	&__title {
		font-size: 1.4rem;
		text-transform: uppercase;
		font-weight: 700;
		letter-spacing: 1.4px;
		margin-left: 4rem; }

	@include min($xl) {
		display: flex;
		left: -5%; }
	@include min(1800px) {
		left: 4.5%; } }
