.b-benefit {
	position: relative;
	z-index: 1;
	display: block;
	color: $color-dark;

	padding: 3.4rem 2rem;

	background: transparent no-repeat center;
	background-size: cover;

	min-height: 35rem;
	max-width: 29rem;
	margin: 0 auto;
	&:before {
		content: '';
		z-index: -1;
		position: absolute;
		display: block; }
	&:after {
		content: '';
		z-index: -1;
		position: absolute;
		display: block;
		background: transparent no-repeat center;
		background-size: contain; }
	&--style-1 {
		&:after {
			top: 0;
			right: 0; } }

	&--style-2 {
		min-height: 35rem;
		background-position: top center;
		&:before {
			bottom: 0;
			left: 0;
			right: 0;
			background-image: linear-gradient(to top, #222222 0%, rgba(34, 34, 34, 0) 100%);
			min-height: 40%;
			opacity: 0.6; }
		&:after {
			bottom: 0;
			right: 0; } }
	&--style-3 {
		color: #ffffff;
		&:after {
			top: 0;
			left: 0; }
		&:before {
			right: 0;
			top: 0;
			bottom: 0;
			min-width: 80%;
			background-image: linear-gradient(to left, #000000 33%, rgba(0, 0, 0, 0) 100%);
			opacity: 0.64; } }
	&__content {
		&--right {
			max-width: 20rem;
			margin-left: auto; } }
	&__caption {
		display: block;
		font-weight: bold;
		font-size: 2.6rem;
		line-height: 1;
		margin-bottom: .6rem; }
	&__desc {
		font-size: 1.6rem;
		display: block;
		font-weight: 500; }
	&__label {
		display: inline-block;
		font-size: 1.2rem;
		font-weight: 700;
		line-height: 1;
		letter-spacing: 0.1em;
		color: #ffffff;
		text-transform: uppercase;
		padding: 1.1rem 1.5rem;
		background-color: $color-accent;
		margin-bottom: 1.5rem;

		&--red {
			background-color: $color-red; } }
	@include max($max-lg) {
		&:after {
			width: 121px;
			height: 115px; }
		&--style-1 {
			&:after {
				background-image: url(data:image/svg+xml;base64,); } }
		&--style-2 {
			&:after {
				background-position: bottom right;
				background-image: url(data:image/svg+xml;base64,); } }
		&--style-3 {
			&:after {
				background-position: 0 0;
				background-image: url(data:image/svg+xml;base64,); } } }
	@include min($sm1) {
		padding: 5rem 2rem;
		min-height: 19rem;
		min-width: 100%;
		max-width: 100%;
		&--style-2 {
			min-height: 4.4rem;
			&:after {} } }

	@include min($lg) {
		min-height: 32rem;
		padding: 6.9rem 4rem;
		&--style-1 {
			&:after {
				width: 435px;
				height: 236px;
				background-image: url(data:image/svg+xml;base64,); } }

		&--style-2 {
			min-height: 66rem;
			&:after {
				width: 346px;
				height: 454px;
				background-image: url(data:image/svg+xml;base64,); } }
		&--style-3 {
			&:after {
				width: 402px;
				height: 235px;
				background-image: url(data:image/svg+xml;base64,); } }
		&__content {
			&--right {
				max-width: 25rem; } }
		&__caption {
			font-size: 3.8rem; }
		&__desc {
			font-size: 2rem; } } }
