.slick-dots {
  bottom: 0;
}

.slick-dots li {
  width: auto;
  height: auto;
  margin: .5rem;
}

.slick-dots li button {
  width: 1rem;
  height: 1rem;
  margin: 0;
  background-color: #d8dde6;
  border-radius: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
}

.slick-dots li button:before {
  position: absolute;
  display: none;
  left: -.2rem;
  top: -.2rem;
  border: 1px solid rgba(246, 139, 33, 0.4);
  font-size: 0;
  width: calc(100% + .4rem);
  height: calc(100% + .4rem);
}

.slick-dots li.slick-active button {
  background-image: linear-gradient(to left, #f25920 0%, #f68b21 100%);
}

.slick-dots li.slick-active button:before {
  display: block;
}

.o-slick {
  position: relative;
  padding-bottom: 35px;
}

.o-slick--gutter {
  margin-left: -1rem;
  margin-right: -1rem;
}

.o-slick__item {
  padding: 0 1rem;
}

.o-slick__arrow {
  position: absolute;
  top: calc(50% - 35px);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  border: 1px solid #efefef;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.o-slick__arrow:before, .o-slick__arrow:after {
  content: '';
  position: absolute;
  display: block;
  width: 3px;
  height: 3px;
  background-color: #f68b21;
}

.o-slick__arrow--prev {
  left: -7.6rem;
}

.o-slick__arrow--next {
  right: -7.6rem;
}

.o-slick__arrow:before {
  top: -1px;
  right: -1px;
}

.o-slick__arrow:after {
  bottom: -1px;
  left: -1px;
}

.o-slick__arrow svg {
  font-size: 1.4rem;
  color: #ff712b;
}

.o-slick__arrow:hover {
  cursor: pointer;
  background-image: linear-gradient(to left, #f25920 0%, #f68b21 100%);
  border-color: transparent;
  box-shadow: 0 20px 60px rgba(28, 46, 68, 0.08);
}

.o-slick__arrow:hover:before, .o-slick__arrow:hover:after {
  display: none;
}

.o-slick__arrow:hover svg {
  color: #ffffff;
}

