.b-image-decoration {
	position: relative;
	display: inline-block;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		display: block;
		background: transparent no-repeat center;
		background-size: contain; }

	@include max($max-lg) {
		&:after {
			width: 121px;
			height: 115px;
			background-image: url(data:image/svg+xml;base64,); } } }
